.toggle {
    display: flex;
    flex-direction: column;
    gap: .75rem;

    div {
        display: flex;
        align-items: center;
        gap: .5rem;
        font-weight: 600;
        margin: 0;
        padding: 0;
        border: none;
        background: none;
        cursor: pointer;
        outline: none;
        user-select: none
    }
}